<template>
  <div id="" class="bg-light p-0" style="">
    <textarea readonly id="textarea" style="position: fixed; top: -300vh'"></textarea>
    <!-- <div class="video-background"> -->

    <iframe
      id="video"
      width="100%"
      height="100%"
      :src="`${src}?theme=dark&autoplay=1&autohide=0&cc_load_policy=1&modestbranding=1&fs=0&showinfo=0&rel=0&iv_load_policy=3&mute=0&loop=0&enablejsapi=1&version=3&playerapiid=ytplayer&controls=1&mode=opaque&showinfo=0;`"
      allow="autoplay;"
      sandbox="allow-forms allow-scripts allow-pointer-lock allow-same-origin allow-top-navigation"
    />
    <!-- </div> -->
    <div @click="toggleVideoPlay" class="cover-top"></div>
    <!------------------------------------->
    <div @click="$refs.ctrl.classList.toggle('show')" class="toggleCtrl">
      <i class="fa fa-cogs"></i>
    </div>
    <!------------------------------------->

    <div ref="ctrl" class="ctrl_bar">
      <div v-if="src != ''" class="ctrl_btns bg-dark">
        <div class="ctrl_btn">
          <div class="slidecontainer">
            <input
              type="range"
              :step="0.25"
              :min="0.25"
              :max="2"
              @change="setSpeed"
              v-model="rate"
              class="slider"
              id="myRange"
            />
          </div>
          <label>speed</label>
        </div>
        <div class="ctrl_btn">
          <div class="slidecontainer">
            <input
              type="range"
              :step="1"
              :min="0"
              :max="100"
              @change="setVolume"
              v-model="volume"
              class="slider"
              id="myRange"
            />
          </div>
          <label>volume</label>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Capacitor } from "@capacitor/core";
import Axios from "axios";
// import html from "./iframe-html-2";
import Copy from "./preventCopyPlugin";

export default {
  props: ["src"],
  mounted() {
    // this.initialize();
    this.mobile = Capacitor.isNative;
    this.deleteClipboard();
    setTimeout(() => {
      const vid = document.getElementById("video");
      vid.contentWindow.postMessage(
        '{"event":"command","func":"' + "playVideo" + '","args":""}',
        "*"
      );
      this.played = true;
    }, 2200);
  },
  beforeDestroy() {
    console.log("destroyiiiiiiiiiiiiing");
    clearTimeout(this.setTimeOut);
    this.setTimeOut = "stop";
  },
  data() {
    return {
      played: false,
      mobile: false,
      setTimeOut: null,
      rate: 1,
      volume: 50,
      player: null,
      copyCount: 0,
      /////
      focused: false,
    };
  },
  computed: {
    watermark() {
      return this.$store.getters.adminData.watermark;
    },
  },
  methods: {
    fullscreen() {
      const video_con = document.getElementsByClassName("video")[0];
      video_con.classList.toggle("fullscreen");
    },
    async deleteClipboard() {
      console.log("override");
      if (this.setTimeOut == "stop") return;
      this.setTimeOut = setTimeout(async () => {
        this.copyToClipboard("كان غيرك اشطر")
          .then(() => {})
          .catch((e) => {});
        this.deleteClipboard();
      }, 300);
    },
    async copyToClipboard(textToCopy) {
      this.copyCount++;
      // if (this.mobile) {
      // Copy.preventCopy({ value: "security" + this.copyCount });
      if (typeof Android !== "undefined" && Android !== null) {
        Android.preventCopy("security" + this.copyCount);
      }
      // }
      // if (navigator.clipboard != undefined) {
      //   console.log("navigator clipboard");
      //   return new Promise(async (res, rej) => {
      //     try {
      //       await navigator.clipboard.writeText("security" + this.copyCount);
      //       res("copied");
      //     } catch (e) {
      //       rej(e);
      //     }
      //   });
      // } else if (window.clipboardData) {
      //   console.log("window clipboard");
      //   return new Promise((res, rej) => {
      //     window.clipboardData.setData("Text", "security" + this.copyCount);
      //     res("copied2");
      //   });
      // } else {
      else {
        const el = document.getElementById("textarea");
        el.value = "security" + this.copyCount;
        el.select();
        // document.body.removeChild(el);
        return new Promise((res, rej) => {
          document.execCommand("copy") ? res("copied3") : rej("error");
        });
      }
      // }
    },
    /////////////////////////////////////////////////////////
    /////////////////////////////////////////////////////////
    /////////////////////////////////////////////////////////
    toggleVideoPlay() {
      const vid = document.getElementById("video");
      if (this.playing) {
        vid.contentWindow.postMessage(
          '{"event":"command","func":"' + "pauseVideo" + '","args":""}',
          "*"
        );
        this.playing = false;
      } else {
        vid.contentWindow.postMessage(
          '{"event":"command","func":"' + "playVideo" + '","args":""}',
          "*"
        );
        this.playing = true;
      }
    },
    setSpeed() {
      const rate = +this.rate;
      const vid = document.getElementById("video");
      const comm = {
        event: "command",
        func: "setPlaybackRate",
        args: [rate, true],
      };
      vid.contentWindow.postMessage(JSON.stringify(comm), "*");
    },
    setVolume() {
      const vol = +this.volume;
      const vid = document.getElementById("video");
      const comm = {
        event: "command",
        func: "setVolume",
        args: [vol, true],
      };
      vid.contentWindow.postMessage(JSON.stringify(comm), "*");
    },
    seekTo() {
      const vid = document.getElementById("video");
      const comm = {
        event: "command",
        func: "seekTo",
        args: [200, true],
      };
      vid.contentWindow.postMessage(JSON.stringify(comm), "*");
    },

    ///////////////////////////////////////////////////////////
    ///////////////////////////////////////////////////////////
    ///////////////////////////////////////////////////////////
    async initialize() {
      const html2 = html.replace("##src##", this.src.split("/embed/")[1]);
      const doc = this.htmlToDocument(html2);
      this.updateIframe(doc);
    },
    htmlToDocument(html) {
      const parser = new DOMParser();
      const doc = parser.parseFromString(html, "text/html");
      return doc;
    },
    updateIframe(doc) {
      const iframe = this.$refs.frame;
      const iframeDocument = iframe.contentWindow.document;
      iframeDocument.open();
      iframeDocument.write(doc.documentElement.innerHTML);
      iframeDocument.close();
    },
  },
};
</script>

