<script>
import VideoIframe from "./video-viewer/video_iframe.vue";
import VimeoIframe from "./video-viewer/vimeo_iframe.vue";
import VideoStream from "./video-viewer/video_stream.vue";
import YoutubeStream from "./video-viewer/youtube-stream.vue";
import AttachmentViewer from "./attachment-viewer/attachment-preview";
import { Capacitor, FilesystemDirectory, Plugins } from "@capacitor/core";
import axios from "axios";
const { Filesystem } = Plugins;
import { url, eventBus } from "../../main";

export default {
  props: ["lesson", "userData"],
  components: {
    VideoIframe,
    VimeoIframe,
    VideoStream,
    YoutubeStream,
    AttachmentViewer,
  },
  created() {
    this.mobile = Capacitor.isNative;
    this.getUserTaskAndSolution();

    this.setTimeOut2 = setTimeout(() => {
      console.log("add to views");
      axios
        .post("/addVideoView", {
          userId: this.$store.getters.userId,
          lessonId: this.lesson._id,
        })
        .then((res) => {});
    }, 120000);
  },
  destroyed() {
    clearTimeout(this.setTimeOut2);
  },
  data() {
    return {
      pdf: null,
      mobile: false,
      loading: false,
      videoStarted: false,
      vidPassword: null,
      examId: null,
    };
  },
  computed: {
    videoType() {
      const video = this.lesson.videoPath;
      if (!video.includes("http")) return "storage-stream";
      else if (video.includes("embed") && video.includes("youtube"))
        return "youtube-embed";
      else if (!video.includes("embed") && video.includes("youtube"))
        return "youtube-stream";
      else if (video.includes("vimeo")) return "vimeo-embed";
    },
    watermark() {
      return this.$store.getters.adminData.watermark;
    },
  },
  watch: {
    lesson() {},
    videoStarted(val) {
      this.$emit("videoStarted", val);
      this.$store.dispatch("toggleHeader", !val);
    },
  },
  methods: {
    async downloadFile(id) {
      const fileUrl = url() + "/downloadFile/" + id;
      if (!Capacitor.isNative || Capacitor.isNative) {
        window.open(fileUrl, "_blank");
      } else {
      }
    },
    downloadFile2(link) {
      window.location.href = link;
    },
    startVideo() {
      if (this.lesson.videoPath.length < 20) return alert("لا يوجد فيديو");

      ////////////////////////////////////
      if (this.videoType == "youtube-stream") {
        axios.get("/authorizeVideo/" + this.$route.params.unit).then((res) => {
          if (res.data.err) return this.$store.dispatch("writemessage", res.data.err);
          this.vidPassword = res.data.password;
          this.videoStarted = true;
        });
      } else {
        this.videoStarted = true;
      }
    },
    getUserTaskAndSolution() {
      this.examId = null;
      axios
        .post("/getUserTaskAndSolution", {
          lessonId: this.lesson._id,
        })
        .then((res) => {
          console.log(res.data);
          this.examId = res.data.examId;
        });
    },
    gotoTask() {
      this.$router.push(
        `/videos/${this.$route.params.unit}/task-controller/${this.examId}/${this.lesson._id}`
      );
    },
    openFile(link) {
      window.open(link, "blank");
    },
  },
};
</script>

