<template>
  <div class="pdf-cont">
    <!-- <iframe width="100%" height="100%" :src="pdf" frameborder="0"></iframe> -->
    <div v-if="!loaded" class="center">
      <loading />
    </div>
    <div id="pdf-viewer">
      <pdf
        v-if="src && src.length > 10"
        :src="src.replace('http://', 'https://')"
        :page="page"
        @num-pages="(e) => (totalPages = e)"
        @loaded="loaded = true"
        @error="reload"
        style="display: block; width: 100%; height: 100%"
      ></pdf>
    </div>
    <div class="pdf-footer">
      <div class="slider-1">
        <input
          type="range"
          :min="1"
          :max="totalPages"
          :step="1"
          v-model.number="currentPage"
        />
      </div>
      <div class="row g-2 align-items-center footer-bottom">
        <div class="col-1 center">
          <i @click="prev" class="fa fa-arrow-left"></i>
        </div>
        <div class="col-md-2 col-6">
          <input
            class="form-control w-100"
            type="number"
            min="1"
            :max="totalPages"
            v-model.number="currentPage"
          />
        </div>
        <div class="col-1 center">
          <i @click="next" class="fa fa-arrow-right"></i>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Pdf from "vue-pdf";

export default {
  components: {
    Pdf,
  },
  props: ["pdf"],
  created() {
    this.src = this.pdf;
    console.log(this.src);
  },
  destroyed() {
    window.URL.revokeObjectURL(this.pdf);
  },
  mounted() {
    this.loading = true;
    // this.src.promise.then((pdf) => {
    //   this.totalPages = pdf.totalPages;
    // });
    document.addEventListener("keydown", (e) => {
      if (e.keyCode == 39) {
        this.next();
      } else if (e.keyCode == 37) this.prev();
    });
    this.onScroll();
  },
  data() {
    return {
      src: null,
      loading: false,
      currentPage: 1,
      totalPages: 1,
      scrollTop: 0,
      page: 1,
      progress: 0,
      loaded: false,
      change: true,
      showCtrls: true,
    };
  },
  watch: {
    currentPage(val) {
      if (this.change) {
        this.page = val;
        this.change = false;
        setTimeout(() => {
          this.change = true;
        }, 500);
      }
    },
  },
  methods: {
    onScroll() {
      const viewer = document.getElementById("pdf-viewer");
      viewer.onscroll = (e) => {
        if (viewer.scrollTop <= this.scrollTop) return;
        this.scrollTop = viewer.scrollTop;
        if (viewer.scrollHeight - viewer.scrollTop < 600) {
          this.showCtrls = false;
        } else {
          this.showCtrls = true;
        }
      };
    },
    reload(err) {
      console.log(err);
      this.src = null;
      setTimeout(() => (this.src = this.pdf), 1000);
    },
    next() {
      if (this.currentPage < this.totalPages) this.currentPage++;
    },
    prev() {
      if (this.currentPage > 1) this.currentPage--;
    },
  },
};
</script>

