<template>
  <div>
    <lesson v-if="lesson" :lesson="lesson" :userData="userData" />
    <div v-if="loading" class="center">
      <loading color="#fff" />
    </div>
  </div>
</template>

<script>
import Lesson from "./lesson.vue";
import Axios from "axios";
export default {
  components: {
    Lesson,
  },
  created() {
    const id = this.$route.params.id;
    this.openLesson(id);
    // Axios.get("/fetchVideo/" + id).then((res) => (this.lesson = res.data.video));
  },
  data() {
    return {
      lesson: null,
      loading: false,
    };
  },
  computed: {
    adminData() {
      return this.$store.getters.adminData;
    },
    userData() {
      return {
        username: this.$store.getters.email,
        phone: this.$store.getters.phone ?? "",
      };
    },
  },
  methods: {
    async openLesson(id, code) {
      this.loading = true;
      Axios.post("/fetcLessonForUser", {
        code,
        lessonId: id,
        userId: this.$store.getters.userId,
      }).then((res) => {
        this.loading = false;
        if (res.data.err) {
          alert(res.data.err.message);
          if (res.data.err.code) {
            const code = prompt("قم بأدخال الكود");
            if (code) this.openLesson(id, code);
          }
          // this.$router.replace("/videos/" + this.$route.params.unit);
        }
        this.lesson = res.data.lesson;
      });
      ///////////////////////////////////////
    },
  },
};
</script>

<style lang="scss" scoped></style>
