<template>
  <div class="p-1 w-100">
    <div v-if="file.length < 20" class="center">
      <h3>ليس هناك ملفات ملحقة</h3>
    </div>
    <div v-if="file.length > 20" class="center">
      <div v-if="type == 'pdf'" class="pdf-viewer bg-dark">
        <!-- <pdf-viewer :pdf="file" /> -->
        <iframe :src="file" height="300px" width="100%" frameborder="0"></iframe>
      </div>
      <!--------------------------------------------------------------------------->
      <!--------------------------------------------------------------------------->
      <!--------------------------------------------------------------------------->
      <div v-if="type == 'image'" class="image">
        <v-zoomer
          style="width: 100%; min-height: 200px; border: solid 1px silver"
          class="center"
        >
          <!---IMAGE--->
          <img
            class="img-fluid"
            @click="onImageError"
            @error="onImageError"
            :src="file.replace('http://', 'https://')"
            loading="lazy"
            style="object-fit: contain; width: 100%; height: 100%"
          />
        </v-zoomer>
      </div>
      <!----------------------------------------------------->
      <!----------------------------------------------------->
      <!----------------------------------------------------->

      <div v-if="type == 'video'" class="">
        <iframe
          :src="file"
          style="max-width: 600px"
          height="300px"
          width="100%"
          frameborder="0"
        ></iframe>
      </div>
      <!----------------------------------------------------->
      <!----------------------------------------------------->
      <!----------------------------------------------------->

      <div v-if="type == 'audio'">
        <audio controls>
          <source :src="file" />
        </audio>
      </div>
      <!----------------------------------------------------->
      <!----------------------------------------------------->
      <!----------------------------------------------------->
      <div v-if="type == 'download'" class="row justify-content-center">
        <div class="col-md-4 center">
          <v-btn @click="downloadFile()" depressed color="success"> تحميل الملف </v-btn>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import PdfViewer from "./pdf-viewer";
export default {
  components: {
    PdfViewer,
  },
  props: ["file"],
  created() {},
  computed: {
    type() {
      let type = "download";
      if (this.file.endsWith(".mp3") || this.file.endsWith(".mpeg")) type = "audio";
      else if (this.file.endsWith(".pdf")) type = "pdf";
      else {
        [".jpg", ".jpeg", ".png", ".webp"].forEach((f) => {
          console.log(this.file.endsWith(f.toLowerCase()));
          if (this.file.endsWith(f.toLowerCase())) type = "image";
        });
      }
      return type;
    },
  },
  methods: {
    downloadFile() {
      if (this.type == "download") window.open(this.file, "blank");
    },
  },
};
</script>

<style lang="scss" scoped>
.pdf-viewer {
  // min-height: 700px;
  height: 1000px;
  // max-height: 1400px;
  width: 100%;
  @media (max-width: 900px) {
    height: 500px;
  }
  iframe {
    width: 100%;
    height: 100%;
  }
}
.image {
  width: 50%;
  @media (max-width: 900px) {
    width: 95%;
  }
}
</style>
